.landing-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  position: relative;
}

.logo {
  font-size: 4rem;
  font-weight: 900;
  color: #ff0000;
  text-shadow: 0 0 20px #ff0000, 0 0 40px #ff0000;
  margin-bottom: 1rem;
  animation: pulse 2s infinite;
}

.tagline {
  font-size: 1.5rem;
  color: #00ff41;
  margin-bottom: 3rem;
  text-shadow: 0 0 10px #00ff41;
}

.analyzer {
  width: 500px;
  height: 300px;
  border: 2px solid #00ff41;
  border-radius: 10px;
  background: rgba(0, 255, 65, 0.05);
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(0, 255, 65, 0.3);
}

.analyzer-header {
  background: rgba(0, 255, 65, 0.2);
  padding: 10px;
  font-size: 0.9rem;
  border-bottom: 1px solid #00ff41;
  text-align: center;
}

.scan-area {
  height: 250px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.scan-line {
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #00ff41, transparent);
  position: absolute;
  animation: scan 3s infinite;
  box-shadow: 0 0 10px #00ff41;
}

.scan-text {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  color: #00ff41;
  text-shadow: 0 0 10px #00ff41;
}

.scan-result {
  font-size: 0.9rem;
  color: #ff0000;
  opacity: 0;
  animation: fadeIn 1s forwards;
  animation-delay: 3s;
  text-shadow: 0 0 10px #ff0000;
  text-align: center;
  line-height: 1.4;
}

@media (max-width: 768px) {
  .logo {
    font-size: 2.5rem;
  }
  
  .analyzer {
    width: 90%;
    max-width: 400px;
  }
  
  .tagline {
    font-size: 1.2rem;
  }
}
