.story-section {
  max-width: 1000px;
  margin: 0 auto;
}

.story-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.story-card {
  position: relative;
  min-height: 200px;
  transition: transform 0.3s ease;
}

.story-card:hover {
  transform: translateY(-5px);
}

.card-number {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 3rem;
  font-weight: 900;
  color: rgba(255, 0, 0, 0.3);
  line-height: 1;
}

.story-footer {
  text-align: center;
  background: rgba(255, 0, 0, 0.05);
  border-color: rgba(255, 0, 0, 0.3);
}

.story-footer h3 {
  color: #ff0000;
  text-shadow: 0 0 10px #ff0000;
  margin-bottom: 1.5rem;
}

.directive-signature {
  margin-top: 1.5rem;
  font-style: italic;
  color: #ff0000;
  font-weight: 700;
  text-shadow: 0 0 5px #ff0000;
}

@media (max-width: 768px) {
  .story-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .card-number {
    font-size: 2rem;
  }
}
